	module.exports = {"data":{"wordpressPost":{"title":"Urban cycling, physician attire and the impact of complaints on Doctors: Most Read Articles in January","content":"<p>Several new papers made the January top 10 most read, including the cross-sectional survey by Bourne <em>et al</em>., on the impact of complaints on doctor&#8217;s health and psychological welfare. We also had a popular analysis of cycling injuries and the relationship with personal, trip, route and crash characteristics by Cripton <em>et al</em>. Petrelli <em>et al.</em>&#8216;s paper on the role of physician attire on patient perceptions including trust, satisfaction and confidence and Aboshady <em>et al</em>.&#8217;s article on open online training courses for medical students in developing countries were both highly accessed.</p>\n<p>&nbsp;</p>\n<table border=\"1\" width=\"483\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Cripton <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/1/e006654.full\">Severity of urban cycling injuries and the relationship with personal, trip, route and crash characteristics: analyses using four severity metrics</p>\n<p></a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Bourne <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/1/e006687.full\">The impact of complaints procedures on the welfare, health and clinical practise of 7926 doctors in the UK: a cross-sectional survey</p>\n<p></a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Marston <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/12/e006135.full\">Prescribing of antipsychotics in UK primary care: a cohort study</p>\n<p></a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Caulfield <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/12/e006395.full\">Representations of the health value of vitamin D supplementation in newspapers: media content analysis</p>\n<p></a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Barnett<em> et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/1/e006912.full\">The impact of a streamlined funding application process on application time: two cross-sectional surveys of Australian researchers</p>\n<p></a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Deane <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/12/e006434.full\">Priority setting partnership to identify the top 10 research priorities for the management of Parkinson&#8217;s disease</p>\n<p></a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Petrilli <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/1/e006578.full\">Understanding the role of physician attire on patient perceptions: a systematic review of the literature— targeting attire to improve likelihood of rapport (TAILOR) investigators</p>\n<p></a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Guzman-Castillo <em>et al. </em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/1/e006070.full\">The contribution of primary prevention medication and dietary change in coronary mortality reduction in England between 2000 and 2007: a modelling study</p>\n<p></a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Aboshady <em>et al.</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/5/1/e006804.full\">Perception and use of massive open online courses among medical students in a developing country: multicentre cross-sectional study</p>\n<p></a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Parand <em>et al</em>.</td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/4/9/e005055.full\">The role of hospital managers in quality and patient safety: a systematic review</p>\n<p></a></td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n<p>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"70ce4fa4-c068-5acb-a566-45af142e9e6e"}}

